/**
 * Visual grid overlay
 */
.wca-section {
  position: relative;
}
.wca-section--tablet .wca-section__overlay {
  grid-template-columns: repeat(8, 1fr);
}
.wca-section--mobile .wca-section__overlay {
  grid-template-columns: repeat(4, 1fr);
}
.wca-section.is-selected .wca-section__overlay, .wca-section.has-child-selected .wca-section__overlay {
  position: absolute;
}
.wca-section.is-selected .wca-section__overlay-col, .wca-section.has-child-selected .wca-section__overlay-col {
  box-shadow: -0.5px 0 0 0 currentColor, 0.5px 0 0 0 currentColor;
}
.wca-section__overlay {
  position: relative;
  pointer-events: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: grid;
  grid-gap: 1px;
  grid-template-columns: repeat(12, 1fr);
  color: rgba(0, 0, 0, 0.1);
  z-index: 0;
}

