/*--------------------------------------------------------------
# Heading Labels
--------------------------------------------------------------*/
.is-wca-heading-label-on {
	.editor-styles-wrapper {
		.wp-block[data-type="core/heading"]:not(.is-selected):not(.is-hovered) {
			position: relative;

			&::before {
				font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
				position: absolute;
				top: 8px;
				left: -25px;
				font-weight: 500;
				font-size: 12px;
				opacity: 0.5;
			} 
		}

		@for $i from 1 to 6 {
			h#{$i}[data-type="core/heading"]:not(.is-selected):not(.is-hovered)::before {
				content: "H#{$i}";
			}
		}

		.editor-inner-blocks,
		.block-editor-inner-blocks {
			.wp-block[data-type="core/heading"]:not(.is-selected):not(.is-hovered)::before {
				content: "";
			} 
		}
	}

	&.is-guide-lines-on {
		.editor-styles-wrapper {
			.wp-block[data-type="core/heading"]:not(.is-selected):not(.is-hovered)::before {
				content: "";
			}
		}
	}
}
